.preview-form__block {
  padding: 64px 48px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #5658dd;
  background-image: url(/src/img/form.svg);
  background-position: 100% 100%;
  background-repeat: no-repeat;
}

.preview-form__slogan {
  margin-right: 48px;
}

.preview-form__slogan h3 {
  color: #eceded;
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: -0.3px;
}

.action__input {
  position: relative;
  background-color: #eceded;
  border: 1px solid #151719;
  padding: 11px 46px 11px 15px;
  display: block;
  line-height: 24px;
  letter-spacing: -0.1px;
  height: 48px;
  color: #151719;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-color: transparent;
  border-radius: 2px;
  width: 100%;
}

.action__input input {
  font-size: 16px;
  background-color: #eceded;
}

.icon {
  position: absolute;
  width: 16px;
  height: auto;
  top: 50%;
  right: 15px;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  cursor: pointer;
  color: #5658dd;
}

@media (max-width: 640px) {
  .preview-form__block {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 48px 16px;
  }
  .preview-form__slogan {
    text-align: center;
    margin: 0 auto 40px;
  }
  .preview-form__slogan h3 {
    font-size: 24px;
    line-height: 34px;
    letter-spacing: -0.1px;
  }
  .preview-form__action {
    width: 100%;
  }
}
