@media (max-width: 640px) {
  .navbar__flex {
    position: static;
  }
  .navbar__navigation {
    position: absolute;
    background: #0e0f11;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 500;
    max-height: 0px;
    overflow: hidden;
    -webkit-transition: max-height 0.7s ease-in-out, opacity 0.7s;
    transition: max-height 0.7s ease-in-out, opacity 0.7s;
    opacity: 0;
  }
  .navbar__navigation.active {
    opacity: 1;
    -webkit-transition: max-height 0.25s ease-in-out, opacity 0.15s;
    transition: max-height 0.25s ease-in-out, opacity 0.15s;
    max-height: 156px;
  }
  .navbar__block-for-mob {
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    min-height: 156px;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    padding: 24px;
  }
  .navbar__for-mob-top {
    width: 100%;
    margin: 0 auto;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .navbar__for-mob-bottom {
    width: 100%;
    margin: 0 auto;
    -webkit-box-flex: 0;
        -ms-flex-positive: 0;
            flex-grow: 0;
  }
  .navbar__link.navigation__item {
    padding-top: 12px;
    padding-bottom: 12px;
    color: #eceded;
    font-weight: 500;
  }
  .navbar__btn.navigation__item {
    margin-left: 0;
    margin-top: 12px;
    margin-bottom: 12px;
    width: 100%;
    max-width: 280px;
  }
  .navbar__btn.navigation__item a {
    padding: 5px 23px;
    height: 36px;
    width: 100%;
    max-width: 280px;
  }
  .navbar__btn-burger {
    z-index: 10;
    display: block;
    position: relative;
    width: 24px;
    height: 24px;
    background-color: #151719;
    cursor: pointer;
  }
  .navbar__btn-burger.active::after {
    bottom: 50%;
    -webkit-transform: rotate(405deg) translate(0, 50%);
            transform: rotate(405deg) translate(0, 50%);
    margin-bottom: 0px;
  }
  .navbar__btn-burger.active::before {
    top: 50%;
    -webkit-transform: rotate(-405deg) translate(0, -50%);
            transform: rotate(-405deg) translate(0, -50%);
    margin-top: 0px;
  }
  .navbar__btn-burger.active span {
    -webkit-transform: scale(0) translate(0px, -50%);
            transform: scale(0) translate(0px, -50%);
  }
  .navbar__btn-burger span {
    position: absolute;
    width: 100%;
    height: 10%;
    left: 0;
    background-color: #9ca9b3;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    top: 50%;
    -webkit-transform: scale(1) translate(0px, -50%);
            transform: scale(1) translate(0px, -50%);
  }
  .navbar__btn-burger::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 10%;
    left: 0;
    background-color: #9ca9b3;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    bottom: 0;
    margin-bottom: 5px;
  }
  .navbar__btn-burger::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 10%;
    left: 0;
    background-color: #9ca9b3;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    top: 0;
    margin-top: 5px;
  }
}
